/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { useState, useEffect, } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { useSnackbar } from 'notistack';

// @mui

import {
  Card,
  Table,
  Button,
  Checkbox,
  TableRow,
  TableBody,
  TableCell,
  Container,
  Typography,
  TableContainer,
  TablePagination,
  Switch,
} from '@mui/material';
// swal 
import swal from 'sweetalert';
// format
import FormControlLabel from '@mui/material/FormControlLabel';
// routes
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Iconify from '../../components/Iconify';
import Scrollbar from '../../components/Scrollbar';
import SearchNotFound from '../../components/SearchNotFound';

import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// sections
import { UserListHead, AdexToolbar } from '../../sections/admin/user/list';


// axios

import axios from '../../utils/axios'; 

// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'username', label: 'Username', alignRight: false },
  { id: 'account_name', label: 'Account Name', alignRight: false },
  { id: 'bank_name', label: 'Bank Name', alignRight: false },
  {id: 'account_number', label: 'Account Number', alignRight: false},
  {id: 'bank_code', label: 'Bank Code', alignRight: false},
];

// ----------------------------------------------------------------------

export default function AdexUserBank() {
    const { themeStretch } = useSettings();
    const [userList, setUserList] = useState([]);
    const [page, setPage] = useState(0);
  const [load,SetLoad] = useState(true);
  const [totalPage, SetTotal] = useState(0);
  const [selected, setSelected] = useState([]);
    const [filterName, setFilterName] = useState('');
    const [rowsPerPage, setRowsPerPage] = useState(5);
    const [dense, Setdense] = useState(false);
    const [isNotFound, SetNotFound] = useState(false);
  const { enqueueSnackbar } = useSnackbar();
  const AccessToken =  window.localStorage.getItem('accessToken');

  useEffect(()  => {
    initialize(page,rowsPerPage,filterName);
}, []);

const initialize = async (pag,adex = 5,search) => {
  let adex_page;
  if(pag > page){
     adex_page = pag + 1;
  }else{
     adex_page = pag;
  }
  try{
    SetLoad(true);

 const data = await axios.get(`/api/user/bank/account/details/${AccessToken}/secure?page=${adex_page}&adex=${adex}&search=${search}`)
setUserList(data.data?.userbank?.data);
 SetLoad(false)
 SetTotal(data.data?.userbank?.total)     
 setPage(pag);
 SetNotFound(data.data?.userbank?.total === 0)
}catch(_){
 SetLoad(false);
 setUserList([]);
}
}
 



  const handleSelectAllClick = (checked) => {
    if (checked.target.checked) {
      const newSelecteds = userList.map((n) => n.username);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (username) => {
    const selectedIndex = selected.indexOf(username);
    let newSelected = [];
    if (selectedIndex === -1) {
        newSelected = newSelected.concat(selected, username);
      } else if (selectedIndex === 0) {
        newSelected = newSelected.concat(selected.slice(1));
      } else if (selectedIndex === selected.length - 1) {
        newSelected = newSelected.concat(selected.slice(0, -1));
      } else if (selectedIndex > 0) {
        newSelected = newSelected.concat(
          selected.slice(0, selectedIndex),
          selected.slice(selectedIndex + 1),
        );
      }
    setSelected(newSelected);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    initialize(0,event.target.value,filterName)
    setPage(0);
  };
  
  const handleFilterByName = async(filterName) => {
    setFilterName(filterName);
    setPage(0);
    if(filterName){
      initialize(page,rowsPerPage,filterName);
      }else{
      initialize(page,rowsPerPage,filterName);
      SetNotFound(false)
      SetLoad(false);
     }
  };

  

  const handleDeleteMultiUser = (selected) => {
    swal({
        title: "Note",
        text: `Are You Sure You Want To Delete this user Bank Details`,
        icon: "info",
        buttons: true,
        dangerMode: true,
      })
      .then((willDelete) => {
        if (willDelete) {

            axios.post(`/api/delete/user/adex/bank/hacker/${AccessToken}/system`,{
                username:selected
            }).then(data => {
                console.log(data.data.message)
                enqueueSnackbar(data.data.message)
    const deleteUsers = userList.filter((user) => !selected.includes(user.username));
    setSelected([]);
    setUserList(deleteUsers);  
            }).catch(error => {
                if(error.data?.message){
                    enqueueSnackbar(error.data.message, { variant: 'error' })   
                }else{
                    enqueueSnackbar('An Error Occured', { variant: 'error' })   
                }
            });
        } else {
            swal("Request Cancelled");
          }
        });
  };




  function handleChangeDense(event){
   Setdense(event.target.checked);
  }

  return (
    <Page title="User: Account Details">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="User Account Details"
          links={[
            { name: 'Dashboard', href: PATH_ADMIN.root },
            { name: 'List' },
          ]}
          action={
            <Button
              variant="contained"
              component={RouterLink}
              to={PATH_ADMIN.user.newUser}
              startIcon={<Iconify icon={'eva:plus-fill'} />}
            >
              New User
            </Button>
          }
        />

        <Card>
          <AdexToolbar
            numSelected={selected.length}
            filterName={filterName}
            onFilterName={handleFilterByName}
            onDeleteUsers={() => handleDeleteMultiUser(selected)}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table
              size={dense ? 'small' : 'medium'}
              >
                <UserListHead
                  // order={order}
                  // orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  rowCount={userList.length}
                  numSelected={selected.length}
                  // onRequestSort={handleRequestSort}
                  onSelectAllClick={handleSelectAllClick}
                  onDeleteUsers={() => handleDeleteMultiUser(selected)}
                />

              {!load ?  <> <TableBody>
                  {userList.map((row) => {
                    const { id, username, bank_name, bank, account_number, bank_code,  } = row;
                    const isItemSelected = selected.indexOf(username) !== -1;
                    return (
                      <TableRow
                        hover
                        key={id}
                        tabIndex={-1}
                        role="checkbox"
                        selected={isItemSelected}
                        aria-checked={isItemSelected}
                      >
                        <TableCell>
                          <Checkbox checked={isItemSelected} onClick={() => handleClick(username)} />
                        </TableCell>
                        <TableCell sx={{ display: 'flex', alignItems: 'center' }}>
                          <Typography>
                            {username}
                          </Typography>
                        </TableCell>
                        <TableCell align="left">{bank_name}</TableCell>              
                        <TableCell align="left">{bank}</TableCell>
                        <TableCell align="left">{account_number}</TableCell>
                        <TableCell align="left">{bank_code}</TableCell>
                      </TableRow>
                    );
                  })}
                </TableBody> </> : <TableRow>
                      <TableCell align="center" colSpan={5} sx={{ py: 3 }}>
                      <Typography variant="body2">Loading ...</Typography>
                      </TableCell>
                    </TableRow> }
                {isNotFound && (
                    <TableRow>
                      <TableCell align="center" colSpan={5} sx={{ py: 3 }}>
                        <SearchNotFound searchQuery={filterName} />
                      </TableCell>
                    </TableRow>
                 
                )}
              </Table>
            </TableContainer>
          </Scrollbar>
          
          <FormControlLabel
        control={<Switch checked={dense} onChange={(event) => handleChangeDense(event)} />}
        label="Dense padding" align="left"
      />   
         
         <TablePagination
            rowsPerPageOptions={[5, 10, 25,  100]}
            component="div"
            count={totalPage}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={(e, page) =>  initialize(page,rowsPerPage,status,filterName)}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
            
        </Card>
      </Container>
    </Page>
  );
}

// ----------------------------------------------------------------------

